.about-container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about-me {
    width: 110%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    display: grid;
}

.about-me-img {
    border-radius: 2rem;
    overflow: hidden;
}
.about-me-img img{
}


.about-cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

.about-card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}

.about-card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about-card h5 {
    font-size: 0.95rem;
}

.about-card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about_contant p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

.para {
    display: grid;
    grid-template-rows: 1fr 3.8rem;
    align-items: center;
}
.tech-store{
    font-weight: 500;
    font-size: 20px;
    color: aliceblue;
}

@media screen and (max-width:1024px) {
    .about-container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about-me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_contant p {
        margin: 1rem 0 1.5rem;
    }
    #about{
        height: fit-content;
    }
}

@media screen and (max-width:600px) {
    .about-me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about-cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about_contant {
        text-align: center;
    }
    .about_contant p {
        margin: 1.5rem 0;
    }
    .para {
        display: block;
        
    }
    #about{
        height: fit-content;
    }
}